.game {
    min-height: 100vh;
    width: 100%;
    background-color: black;
    color: white;

    background-image: url('../assets/background7.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.primer-div-game {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 10vh;
}

.logo-button {
    display: flex;
    align-items: center;
    justify-content: center;
}

.playstore-button {
    width: 350px;
    margin: 10px;
    border: 1px solid white;
    border-radius: 10px;

    @media (max-width: 768px) {
        width: 55%;
    }
}

.android-logo {
    width: 100px;
    margin: 10px;

    @media (max-width: 768px) {
        width: 15%;
    }
}