.sider-button {
    margin-bottom: 5px;
    background-color: black;

    @media (max-width: 768px) {
        width: 45px;
    }
}
.sider {
    position: fixed;
    top: 15%;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0);

}
.menu {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    font-family: MiFuente;
    font-size: 12pt;

    &.menu-collapsed {
        // Estilos para el menú retraído
        width: 70px;

        @media (max-width: 768px) {
            width: 45px;
        }
    }
}

.menu .ant-menu-item-selected {
    background: linear-gradient(145deg, rgb(222, 3, 3) 0%, rgb(2, 16, 74) 100%); 
    
}
.menu .ant-menu-item:active {
    background-color: #01080d; /* Cambia el color de fondo del elemento seleccionado o al pasar el mouse sobre él o al presionarlo */
    color: #fff; /* Cambia el color del texto al seleccionar o al pasar el mouse sobre el elemento o al presionarlo */
    /* Agrega otros estilos que desees aplicar al elemento seleccionado, al pasar el mouse sobre él o al presionarlo */
  }