.the-header {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    //background-color: rgba(0, 0, 0, 0.8);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.02) 0%, rgba(0, 0, 0, 0.55) 15%, rgba(0, 0, 0, 0.9) 85%);
    padding: 60px 10%;
    width: 100%;
    z-index: 1;
    position: absolute;

    @media (max-width: 768px) {
        padding: 35px 10%;
    }
    
    @media (max-width: 480px) {
        height: 40px;
    }
}

.logo-image {
    width: 180px;
    padding-top: 15px;

    @media (max-width: 768px) {
        width: 130px;
        padding-top: 20px;
    }
    
    @media (max-width: 480px) {
        width: 90px;
        padding-top: 25px;
    }
}

.button-connect-contenedor {
    padding: 15px;
    display: flex;
    width: 500px;
    justify-content: center;
}

.button-connect {
    color: white;
    font-family: MiFuente;
    font-size: 17pt;
    padding: 15px 30px;
    text-align: center;
    display: flex;
    align-items: center;

    @media (max-width: 768px) {
        
    }
    
    @media (max-width: 480px) {
        font-size: 10pt;
        padding: 5px 15px;
    }
}

.button-connect:hover {
    font-size: 19pt;
    box-shadow: 0px 0px 15px 10px #51505244;
    background: linear-gradient(145deg, rgba(255,18,0,1) 0%, rgba(36,68,255,1) 100%);

    @media (max-width: 768px) {
        
    }
    
    @media (max-width: 480px) {
        font-size: 12pt;
    }
}