.home {
    background-color: black;
    min-height: 100vh;
    width: 100%;
    background-image: url('../assets/background62.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.primer-div {
    padding-top: 10vh;
    padding-bottom: 15vh;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    min-height: 105vh;
    background-color: black;

    @media (max-width: 768px) {
        flex-direction: column;
        align-items: center;
    }
}

.home-title {
    width: 45%;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 10% 0 0 5%;
    flex-direction: column;

    position: relative;
    left: -100%;
    animation: slideInFromLeft 2.5s forwards;


    @media (max-width: 768px) {
        width: 90%;
        font-size: 30pt;
        text-align: center;
        padding: 2% 20%;
    }

    @media (max-width: 480px) {
        font-size: 20pt;
        padding: 0% 10% 0 10%;
    }
}

.hover-magico {
    transition-duration: 0.5s;
}

.hover-magico:hover {
    text-shadow:rgb(255, 0, 0) 0px 2px 2px, rgb(255, 127, 0) -2px 5px 0px, rgb(255, 255, 0) -4px 10px 0px, rgb(0, 255, 0) -8px 15px 0px, rgb(0, 0, 255) -12px 20px 0px, rgb(75, 0, 130) -16px 25px 0px, rgb(148, 0, 211) -20px 30px 0px;
}

.hover-magico-2{
    transition-duration: 0.5s;
}

.hover-magico-2:hover {
    text-shadow:rgb(255, 153, 204) 20px 10px 0px, rgb(100, 165, 183) -15px -6px 0px;
}

.home-title-title-button {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    padding: 5vh 5vw;
    display: flex;
    flex-direction: column;
    align-items: end;

    @media (max-width: 768px) {
        align-items: center;
    }
}



.card-3d {
    height: 80vh;
    width: 45%;

    @media (max-width: 768px) {
        width: 90%;
        height: 70vh;
    }

    @media (max-width: 480px) {
        height: 60vh;
    }


}

.card-3d-card {
    transform: translatey(0px);
    animation: float 6s ease-in-out infinite;
}

.drag {
    width: 100px;
    position: absolute;
    top: 75%;
    left: 73%;
    opacity: 0;
    animation: fadeEffect 2s 10;
}

@keyframes fadeEffect {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.segundo-div {
    background-color: black;

    color: white;
    display: flex;
    flex-direction: column;
    min-height: 110vh;
    align-items: center;
    justify-content: center;
}

.segundo-div-primer-item {
    width: 100%;
    min-height: 50vh;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    padding: 35px 0;


}

.home-item {
    font-size: 30pt;
}

.ficha-home {
    background-color: rgba(0, 0, 0, 0.8);
    width: 250px;
    height: 350px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 10px;
    text-align: center;
    padding: 30px;
    transition: transform 0.3s ease-in-out;
    
    @media (max-width: 480px) {
        max-width: 80vw;
        height: 200px;
    }
}

.ficha-home-image {
    height: 60%;
}

.ficha-home:hover {
    transform: rotate(-5deg);
    background: linear-gradient(145deg, rgba(222, 3, 3, 0.4) 0%, rgba(2, 16, 74, 0.4) 80%);
}

.segundo-div-segundo-item {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5vh;
}

.item-segundo-div-segundo-item {
    margin-top: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 768px) {}

    @media (max-width: 480px) {}
}

.item-text {
    flex: 1;
    transition-duration: 0.4s;
}

.item-text:hover {
    text-shadow:rgb(255, 0, 0) 0px 2px 2px, rgb(255, 127, 0) -2px 5px 0px, rgb(255, 255, 0) -4px 10px 0px, rgb(0, 255, 0) -8px 15px 0px, rgb(0, 0, 255) -12px 20px 0px, rgb(75, 0, 130) -16px 25px 0px, rgb(148, 0, 211) -20px 30px 0px;
}

.item-text-first {
    text-align: end;
}

.coins-logo-small {
    width: 70px;
    transition-duration: 0.5s;

    @media (max-width: 768px) {}

    @media (max-width: 480px) {
        width: 50px;
    }
}

.segundo-div-tercer-item {
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('../assets/background-fire.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.segundo-div-tercer-item-interno {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 70%;
}

.segundo-div-cuarto-item {
    width: 100%;
    min-height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tercer-div {
    padding-top: 25vh;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    color: white;
    text-align: center;
}

.phase {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 20px;
    padding: 25px 20px;
}

.li-roadmap {
    font-size: 25pt;
    font-family: MiFuente;
    transition-duration: 0.5s;

    @media (max-width: 768px) {
        font-size: 18pt;
    }

    @media (max-width: 480px) {
        font-size: 15pt;
    }
}





@keyframes slideInFromLeft {
    to {
        left: 0;
    }
}

@keyframes float {
    0% {
        transform: translatey(0px);
    }

    50% {
        transform: translatey(-20px);
    }

    100% {
        transform: translatey(0px);
    }
}